import React from 'react';

class NumButton extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        value: props.value,
        onClick: props.onClick,
      }
    }
  
    render() {
      return (
        <button className="button" onClick={this.state.onClick}>
          {this.state.value}
        </button>
      )
    }
  
    clickAction() {
        //...
    }
  }

  export default NumButton