import React from 'react';
import './App.css';
import NumButton from './components/NumButton';
import * as LayoutDef from './components/const';
import MissingMjCal from './components/MissingMjCal';

class App extends React.Component {

  constructor() {
    super();
    this.objMissingMjCal = new MissingMjCal();
    this.mjInHand = []
  }

  handleClick(i) {
    if (this.objMissingMjCal.changeMjNum(i, true) === true) {
      this.mjInHand.push(this.renderButton(i));
      this.forceUpdate();
    }
  }

  renderButton(index) {
    return <NumButton value={index} onClick={() => this.handleClick(index)} />
  }

  render() {
    // create mahjong buttons with 1-9
    const btn_mj9 = []
    for (let index = 1; index < 10; index++) {
      btn_mj9.push(this.renderButton(index))
    }

    return (
      <div className="App">
        <header className="App-header">
          {/* print header */}
          {LayoutDef.web_logo}
          {LayoutDef.intro}
        </header>

        <body className="App-body">
          {/* print buttons */}
          <div>{btn_mj9}</div>
          <h3>你的手牌</h3>
          <div>{this.mjInHand}</div>
          <h4>End</h4>
        </body>

        <footer className="App-footer">
          {/* print footer link */}
          {LayoutDef.intro_link}
        </footer>

      </div>
    )
  }
}

export default App;
